.loader {
    width: 300px;
    height: 48px;
    border: 3px solid #fff;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    box-sizing: border-box;
}

.loader::after {
    content: '';
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-bottom-color: #ff3d00;
}

.breed-select {
    width: 300;
}

.ss-main.breed-select {
    width: 300;
}

.loader-text {
    font-family: Arial, sans-serif;
    font-size: 20px;
}

.error {
    color: #ff3d00;
    font-family: Arial, sans-serif;
    font-size: 20px;
}

.js-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.cat-info {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 600px;
    height: 300px;
}

.cat {
    width: auto;
    height: 300px;
    display: block;
    margin-right: 10px;
}

.cat-name {
    font-family: Arial, sans-serif;
    font-size: 24px;
}

.cat-description {
    font-family: Arial, sans-serif;
    font-size: 16px;
}

.temperament {
    font-weight: 600;
}

.cat-temp {
    font-family: Arial, sans-serif;
    font-size: 16px;
}

.is-hidden {
    position:absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    white-space: nowrap;
    clip-path: inset(100%);
