.loader {
  width: 300px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.breed-select, .ss-main.breed-select {
  width: 300px;
}

.loader-text {
  font-family: Arial, sans-serif;
  font-size: 20px;
}

.error {
  color: #ff3d00;
  font-family: Arial, sans-serif;
  font-size: 20px;
}

.js-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  max-width: 600px;
  height: 300px;
  flex-flow: column wrap;
  display: flex;
}

.cat {
  width: auto;
